@media (min-width: 1200px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl,
  .container-xxl {
    max-width: 1080px !important;
  }
  .container.c-blog,
  .container-lg.c-blog,
  .container-md.c-blog,
  .container-sm.c-blog,
  .container-xl.c-blog,
  .container-xxl.c-blog {
    max-width: 900px !important;
  }
}
@media (min-width: 1400px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl,
  .container-xxl {
    max-width: 1200px !important;
  }
  .container.c-blog,
  .container-lg.c-blog,
  .container-md.c-blog,
  .container-sm.c-blog,
  .container-xl.c-blog,
  .container-xxl.c-blog {
    max-width: 1000px !important;
  }
}
