header {
  background-color: #fff;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 3;
}
header .row-header {
  padding: 10px 40px;
  border-bottom: 2px solid #dedede;
  align-items: center;
}
header .row-header img {
  width: 150px;
}
header .row-header .menu-header {
  display: flex;
  justify-content: flex-end;
}
header .row-header .menu-header #nav-desk {
  display: flex;
  align-items: center;
}
header .row-header .menu-header #nav-desk ul {
  display: flex;
  gap: 50px;
  padding: 0;
  margin-bottom: 0;
}
header .row-header .menu-header #nav-desk ul li {
  list-style: none;
}
header .row-header .menu-header #nav-desk ul li a {
  text-decoration: none;
  color: #000;
  font-weight: 300;
  transition: all ease 0.3s;
}
header .row-header .menu-header #nav-desk ul li a:hover {
  color: #ec0001;
}
header .row-header .menu-header #nav-desk ul li.current_page_item a {
  color: #ec0001;
}
header .row-header .menu-header #menu-mobile {
  display: none;
}
header .row-header .menu-header #menu-mobile #nav-mobile {
  width: 0;
  background: #fff;
  position: fixed;
  left: -1000px;
  top: 0;
  height: 100%;
  padding: 30px 0;
  opacity: 0;
  z-index: -1;
  transition: all ease 0.3s;
}
header .row-header .menu-header #menu-mobile #nav-mobile li {
  list-style: none;
  margin: 15px 0;
}
header .row-header .menu-header #menu-mobile #nav-mobile li a {
  font-size: 20px;
  color: #ec0001;
  text-decoration: none;
  font-weight: 600;
}
header .row-header .menu-header #menu-mobile #nav-mobile.active {
  left: 0;
  z-index: 100;
  opacity: 1;
  width: 100%;
}
header .row-header .div-search i {
  color: #ec0001;
  border-left: 1px solid #ec0001;
  padding-left: 5px;
  cursor: pointer;
  transition: all ease 0.3s;
}
header .row-header .div-search i:hover {
  color: #404040;
  border-color: #404040;
}
header .row-header .div-search form input {
  width: 0;
  padding: 0;
  border: none;
  outline: none;
  border-radius: 20px;
  transition: width 0.3s ease, padding 0.4s ease;
  opacity: 0;
  transform: translateX(20px);
}
header .row-header .div-search form.active input {
  width: 100%;
  padding: 5px 10px;
  opacity: 1;
  transform: translateX(0);
  border: 1px solid #ccc;
}

#footer {
  background-color: #404040;
  color: #fff;
  font-size: 14px;
  padding: 10px 0 20px 0;
}
#footer .footer-widgets {
  margin-top: 30px;
}
#footer .footer-widgets .row-links {
  border-bottom: 1px solid #606060;
  padding-bottom: 20px;
  margin-bottom: 20px;
}
#footer .footer-widgets .row-links .logo {
  width: 150px;
  border-right: 1px solid #606060;
  margin-right: 10px;
}
#footer .footer-widgets .row-links nav {
  display: flex;
  align-items: center;
  width: max-content;
}
#footer .footer-widgets .row-links nav ul {
  display: flex;
  gap: 50px;
  padding: 0;
  margin-bottom: 0;
}
#footer .footer-widgets .row-links nav ul li {
  list-style: none;
}
#footer .footer-widgets .row-links nav ul li a {
  font-size: 14px;
  text-decoration: none;
  color: #fff;
  transition: all ease 0.3s;
}
#footer .footer-widgets .row-links nav ul li a:hover {
  color: #ec0001;
}
#footer .footer-widgets .row-links nav ul li:nth-last-child(1) a {
  border: 1px solid #fff;
  padding: 5px 20px;
  border-radius: 20px;
}
#footer .footer-widgets .row-links nav ul li:nth-last-child(1) a:hover {
  background-color: #fff;
  color: #404040;
}
#footer .footer-widgets .row-infos h4 {
  font-size: 14px;
  color: #fff;
  margin-bottom: 10px;
}
#footer .footer-widgets .row-infos a {
  display: flex;
  align-items: center;
  text-decoration: none;
  font-size: 14px;
  color: #fff !important;
  margin: 8px 0;
  transition: all ease 0.3s;
}
#footer .footer-widgets .row-infos a span {
  color: #fff;
  transition: all ease 0.3s;
}
#footer .footer-widgets .row-infos a i {
  color: #fff;
  font-size: 20px;
  margin-right: 8px;
  transition: all ease 0.3s;
}
#footer .footer-widgets .row-infos a:hover {
  color: #ec0001 !important;
}
#footer .footer-widgets .row-infos a:hover span,
#footer .footer-widgets .row-infos a:hover i {
  color: #ec0001 !important;
}
#footer .footer-widgets .row-infos .social-icons {
  display: flex;
  gap: 15px;
  padding: 0;
}
#footer .footer-widgets .row-infos .social-icons li {
  list-style: none;
}
#footer .footer-widgets .row-infos .social-icons li i {
  font-size: 26px;
}
#footer .footer-bottom {
  text-align: center;
  font-size: 12px;
  margin-top: 40px;
}
#footer .footer-bottom p {
  color: #cecece;
  margin-bottom: 0;
}

#incorporadora h2 {
  font-size: 16px !important;
  font-weight: 400;
}
#incorporadora p {
  font-size: 26px;
  font-weight: 500;
  margin: 0;
}

#portfolio .container-fluid {
  border-bottom: 2px solid #999999;
  margin-bottom: 20px;
}
#portfolio #portfolio-posts {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 30px;
}

#video-institucional {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding: 40px 0;
}
#video-institucional span {
  display: flex;
  margin-bottom: 10px;
  font-size: 14px;
  color: #fff;
}
#video-institucional h2 {
  color: #fff;
}
#video-institucional h2.sec-titulo {
  padding-bottom: 15px;
  margin-bottom: 30px;
  border-bottom: 1px solid #fff;
}
#video-institucional p {
  color: #fff;
}

#blog-footer {
  background-color: #404040;
  margin: 0;
  padding-top: 40px;
}
#blog-footer h2,
#blog-footer p {
  color: #fff;
}
#blog-footer .blog-posts {
  margin-top: 30px;
  padding-bottom: 30px;
}
#blog-footer .blog-posts .post {
  transition: all ease 0.3s;
}
#blog-footer .blog-posts .post a {
  text-decoration: none;
  color: #fff;
}
#blog-footer .blog-posts .post a .post-thumbnail {
  margin-bottom: 10px;
}
#blog-footer .blog-posts .post a .post-thumbnail img {
  width: 100%;
  height: auto;
}
#blog-footer .blog-posts .post a h3 {
  font-size: 16px;
  margin: 15px 0 10px 0;
}
#blog-footer .blog-posts .post a p {
  font-size: 12px;
  color: #fff;
  margin-bottom: 0;
}
#blog-footer .blog-posts .post:nth-child(1) {
  padding-right: 45px;
}
#blog-footer .blog-posts .post:nth-child(2) {
  padding: 0 30px;
}
#blog-footer .blog-posts .post:nth-child(3) {
  padding-left: 45px;
}
#blog-footer .blog-posts .post.not-active {
  opacity: 0.4;
}
#blog-footer .blog-posts .post.active {
  opacity: 1;
}

section.page {
  margin: 120px 0 60px;
}
section.page h1 {
  margin-bottom: 40px;
}

#page-building {
  margin-bottom: 0;
}
#page-building h1 {
  margin: 16px 0;
}
#page-building h3 p {
  font-size: 16px;
}
#page-building hr {
  border-top: 2px solid #999999;
  opacity: 1;
  margin: 5px 0 21px;
}
#page-building .experiencia {
  margin-top: 10px;
}
#page-building .middle {
  margin: 80px auto;
}
#page-building .video {
  padding: 0;
}
#page-building .video video {
