* {
  font-family: "Lausanne";
  line-height: 1.5;
  color: #000;
  font-weight: 200;
}

body.modal-open {
  overflow: hidden;
}

h1,
h2 {
  font-size: 26px !important;
}

b {
  font-weight: 400 !important;
}

section {
  margin: 90px 0;
}
section.sec-m {
  margin: 120px 0;
}

input.search-input {
  font-size: 14px;
  margin-left: 5px;
  border-radius: 20px;
  padding: 5px 10px;
  border: 1px solid #d4d4d4;
  outline: none;
  transition: all ease 0.3s;
}

.content-card {
  width: 100%;
  height: 100%;
}
.content-card .thumbnail {
  position: relative;
  overflow: hidden;
}
.content-card .thumbnail img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  cursor: pointer;
  transition: all ease 0.75s;
}
.content-card:hover .thumbnail img {
  transform: scale(1.1);
}

.bg-modal-black {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.85);
}

.bg-modal-white {
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.85);
}

.btn {
  width: 200px;
  border-radius: 30px !important;
  font-size: 14px !important;
  font-weight: 300 !important;
  margin-top: 15px;
}
.btn i {
  margin-right: 8px;
  color: #fff;
}

.primary-button {
  background-color: #ec0001 !important;
  color: #fff !important;
  border: 1px solid #ec0001 !important;
}
.primary-button:hover {
  background-color: #404040 !important;
  border-color: #404040 !important;
}

.secondary-button {
  background-color: #404040 !important;
  color: #fff !important;
  border: 1px solid #fff !important;
}
.secondary-button:hover {
  background-color: #fff !important;
  border-color: #404040 !important;
  color: #404040 !important;
}

.tertiary-button {
  background-color: #fff !important;
  color: #ec0001 !important;
  border: 1px solid #ec0001 !important;
}
.tertiary-button:hover {
  background-color: #ec0001 !important;
  color: #fff !important;
}

.zf-button {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #fbcccc;
  font-size: 14px;
  font-weight: 300;
  color: #ec0001;
  border: none;
  border-radius: 30px;
  width: 120px;
  margin: 0 10px;
  transition: all ease 0.3s;
}
.zf-button i {
  color: #ec0001;
  margin-right: 8px;
  transition: all ease 0.3s;
}
.zf-button:hover, .zf-button.active {
  background: #ec0001;
  color: #fff;
}
.zf-button:hover i, .zf-button.active i {
  color: #fff;
}

.categories-btns {
  position: relative;
}
.categories-btns::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -2px;
  width: 100%;
  height: 2px;
  background-color: #999;
}

.category-btn {
  position: relative;
  display: inline-block;
  margin: 10px 60px;
  border: none;
  background: none;
  color: #000;
  cursor: pointer;
  transition: all ease 0.3s;
}
.category-btn::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -12px;
  width: 0;
  height: 2px;
  background-color: #ec0001;
  transition: all ease 0.5s;
}
.category-btn.active {
  color: #ec0001;
}
.category-btn.active::before {
  content: "";
  width: 100%;
}
.category-btn:nth-child(1) {
  padding-left: 0 !important;
  margin-left: 0 !important;
}

.btn-voltar {
  display: flex;
  align-items: center;
  color: #ec0001;
  text-decoration: none;
  border-left: 1px solid #ec0001;
  padding-left: 10px;
  transition: all ease 0.3s;
}
.btn-voltar i {
  color: #ec0001 !important;
  margin-right: 10px;
  transition: all ease 0.3s;
}
.btn-voltar:hover {
  color: #404040 !important;
  border-color: #404040 !important;
}
.btn-voltar:hover i {
  color: #404040 !important;
}

.slider-item {
  position: relative;
  overflow: hidden;
}

.slider-image {
  position: relative;
}
.slider-image img {
  width: 100%;
  height: auto;
  display: block;
}
.slider-image::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 50%;
  background: linear-gradient(to top, rgb(0, 0, 0), rgba(0, 0, 0, 0));
  pointer-events: none;
}

.slider-text {
  position: absolute;
  bottom: 40px;
  padding: 0 40px;
  color: #fff;
  z-index: 2;
}
.slider-text h2.slider-title {
  font-size: 18px !important;
  margin-bottom: 10px;
}
.slider-text .slider-content {
  font-size: 26px;
}
.slider-text .slider-content p {
  color: #fff;
}

.slick-dots {
  bottom: 20px !important;
  width: max-content;
  right: 30px !important;
}
.slick-dots li {
  width: 15px !important;
  height: 15px !important;
}
.slick-dots li button {
  width: 15px !important;
  height: 15px !important;
}
.slick-dots li button::before {
  width: 15px !important;
  height: 15px !important;
  content: "" !important;
  background-color: #fff;
}
.slick-dots li.slick-active {
  width: 60px !important;
}
.slick-dots li.slick-active button {
  width: 60px !important;
}
.slick-dots li.slick-active button::before {
  width: 60px !important;
  opacity: 1 !important;
}

.slick-arrow {
  z-index: 1;
}

.slick-prev::before,
.slick-next::before {
  font-size: 30px;
  font-family: "Font Awesome 6 Free";
  font-weight: 600;
  transition: all ease 0.3s;
}

.slick-next {
  right: 5%;
}
.slick-next::before {
  content: "\f054";
}

.slick-prev {
  left: 5%;
}
.slick-prev::before {
  content: "\f053";
}

.video-wrapper {
  position: relative;
  padding-top: 56.25%;
}
.video-wrapper iframe,
.video-wrapper video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

#portfolio-posts,
#blog-posts {
  min-height: 500px;
}

.portfolio-item {
  overflow: hidden;
}
.portfolio-item a {
  text-decoration: none;
}
.portfolio-item .thumbnail {
  height: 400px;
  transition: all ease 0.1s;
}
.portfolio-item .portfolio-content {
  padding: 10px 0;
}
.portfolio-item .portfolio-content .top-content,
.portfolio-item .portfolio-content .bottom-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.portfolio-item .portfolio-content .top-content h3,
.portfolio-item .portfolio-content .bottom-content h3 {
  font-size: 26px;
  width: max-content;
  margin: 10px 0;
}
.portfolio-item .portfolio-content .top-content i,
.portfolio-item .portfolio-content .bottom-content i {
  color: #ec0001;
  font-size: 20px;
}
.portfolio-item .portfolio-content .top-content a,
.portfolio-item .portfolio-content .bottom-content a {
  margin-top: 0;
}
.portfolio-item .portfolio-content .metragem {
  display: flex;
  align-items: center;
  font-weight: 600;
  font-size: 14px;
}
.portfolio-item .portfolio-content .metragem img {
  margin-right: 8px;
}
.portfolio-item.principal-item {
  margin-bottom: 80px;
}
.portfolio-item.other-items {
  margin-bottom: 60px;
}

.pagination {
  display: flex;
  justify-content: center;
  margin-top: 30px !important;
}
.pagination .page-numbers {
  display: flex;
  align-items: center;
  padding: 2px 8px;
  margin: 0 5px;
  text-decoration: none;
  color: #ec0001;
  font-weight: 500;
  transition: all ease 0.3s;
}
.pagination .page-numbers:not(.prev, .next) {
  color: #000;
  border-radius: 5px;
  border: 2px solid transparent;
}
.pagination .page-numbers:not(.prev, .next):hover, .pagination .page-numbers:not(.prev, .next).current {
  border-color: #ec0001;
  color: #ec0001;
}

.close-modal {
  position: absolute;
  right: 10px;
  top: 0;
  font-size: 30px;
  z-index: 100;
  cursor: pointer;
  transition: all ease 0.3s;
}
.close-modal:hover {
  color: #ec0001;
}

#form-contato p {
  position: relative;
}
#form-contato p label {
  font-size: 18px;
  width: 100%;
}
#form-contato p label.input-label {
  position: absolute;
  top: 15px;
  left: 0;
  transition: all 0.25s;
  color: rgba(0, 0, 0, 0.4);
