#form-contato p.active input,
#form-contato p.active textarea {
  border-color: #000;
}
#form-contato p.active .input-label {
  font-size: 14px;
  top: 0;
  color: black;
}
#form-contato input,
#form-contato textarea {
  font-size: 16px;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #c7c7c7;
  outline: none;
}
#form-contato input:focus,
#form-contato textarea:focus {
  border-color: #000;
}
#form-contato input[type=text],
#form-contato input[type=email],
#form-contato input[type=tel] {
  width: 100%;
}
#form-contato textarea {
  width: 100%;
  height: 70px;
}
#form-contato .wpcf7-list-item {
  margin: 0;
}
#form-contato .wpcf7-list-item label {
  display: flex;
  align-items: center;
  font-size: 16px;
}
#form-contato .wpcf7-list-item label input {
  margin-right: 8px;
}
#form-contato .wpcf7-list-item label span {
  font-weight: 200 !important;
}
#form-contato .wpcf7-list-item label a {
  color: #000;
  text-decoration: underline;
}
#form-contato input[type=submit] {
  float: right;
}